/* begin-menu */

.nav-link
{
    display: inline-block;
    padding: .5rem 1.5rem !important;
}
.navbar-brand
{
    font-size:25px;
}
.navbar-nav.menu-right
{
    margin-left: 15%;
}
.navbar-nav.menu-right .layer1
{
    position: relative;
    padding: 8px 8px 8px 8px;
    transition: 0.4s;
}
.navbar-nav.menu-right a.nav-link, .navbar-brand
{
    color: #0a70b2!important;
    font-weight: bold;
}
.menu-product .layer2
{
    
    z-index: 4;
    visibility: hidden;
    opacity : 0;
    position: absolute;
    top : 200%;
    left: 43%;
    padding: 2% 3%;
    display: inline-block;
    background-color: white;
    transition: 0.4s;
    border-radius: 2%;
