  width: 70px;
  height: 70px;
  text-indent: -9999px;
  cursor: pointer;
  transition: 0.4s;
}

.previousButton:hover, .nextButton:hover {
  background: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgdmlld0JveD0iMCAwIDI0IDI0Ij48cGF0aCBkPSJNOC4xMjIgMjRsLTQuMTIyLTQgOC04LTgtOCA0LjEyMi00IDExLjg3OCAxMnoiLz48L3N2Zz4=") no-repeat center center / 16px;
}

.previousButton {
left: 0;
-webkit-transform: rotate(180deg) translateY(calc(50% + 0px));
    transform: rotate(180deg) translateY(calc(50% + 0px));
}

.previousButton:hover {
left: -10px;
}

.nextButton {
right: 0;
}

.nextButton:hover {
right: -10px;
}
.slider-content {
  text-align: center;
  position: relative;
}
.slider-content::before
{
    content:'';
    width: 100%;
    height: 100%;
    background-image: -webkit-linear-gradient(left,#0000008a 0%,#12121359 50%,#00000061 100%);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    
}
.slider-content .inner {
  padding: 0 70px;
  box-sizing: border-box;
  position: absolute;
  z-index: 2;
  width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.slider-content .inner button{
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.slider-content .inner h1 {
  font-weight: 900;
  margin: 0 auto;
  max-width: 840px;
  color: #FFFFFF;
  font-size: 64px;
  line-height: 1;
}

.slider-content .inner p {
  color: #FFFFFF;
  font-size: 14px;
  line-height: 1.5;
  margin: 20px auto 30px;
  max-width: 640px;
}

.slider-content section {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.slider-content section span {
  color: #FFFFFF;
}

.slider-content section span {
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
  display: inline-block;
  text-align: left;
  line-height: 1.4;
  vertical-align: middle;
  margin-left: 10px;
}

.slider-content section img {
  width: 40px;
  height: 40px;
  border: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 100%;
  vertical-align: middle;
}

.slider-content section span strong {
  color: #FFFFFF;
  font-size: 14px;
  display: block;
}

@media (max-height: 500px) {
  .slider-wrapper, .slide {
    height: calc(100vh - 75px);
  }    
}

@media (max-width: 640px) {
  .slider-wrapper, .slide {
    height: calc(80vh - 75px);
  }    
}

@media (max-height: 600px) {
  .slider-content .inner h1 {
    font-size: 32px;
  }    
}

@media (max-width: 640px) {
  .slider-content .inner h1 {
    font-size: 32px;
  }
}
/* End-Slide */

/* begin-loading */
.loading-screen
{
    width: 100%;
    height: 100vh;
    background-color: #2e2e2e;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content:center;
}
.loading
{
    width: 80px;
    display: flex;
    flex-wrap: wrap;
    animation: rotate 3s linear infinite;
}
@keyframes rotate
{
    to{
        transform: rotate(360deg);
    }
}
.loading > span
{
    width: 32px;
    height: 32px;
    background-color: red;
    margin : 4px;
    animation: scale 1.5s linear infinite;
}
@keyframes scale
{
    50%{
        transform: scale(1.2);
    }
}
.loading > span:nth-child(1)
{
    border-radius: 50% 50% 0 50%;
    background-color:#e77f67;
    transform-origin: bottom right;
}
.loading > span:nth-child(2)
{
    border-radius: 50% 50% 50% 0%;
    background-color:#778beb;
    transform-origin: bottom left;
    animation-delay: .5s;
}
.loading > span:nth-child(3)
{
    border-radius: 50% 0% 50% 50%;
    background-color:#f8a5c2;
    transform-origin: top right;
    animation-delay: 1.5s;
}
.loading > span:nth-child(4)
{
    border-radius: 0% 50% 50% 50%;
    background-color:#f5cd79;
    transform-origin: top left;
    animation-delay: 1s;
}
/* end-loading */

