.content-inner {
	width: 100%;
	color: #fff;
	position: relative;
	transform-style: preserve-3d;
	perspective: 1000px;
	backface-visibility: hidden;
	transition: .9s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.content-inner > * {
	backface-visibility: hidden;
	transition: .9s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.content-inner:hover .content-front {
	transform: rotateY(-180deg);
}
.content-inner:hover .content-back {
	transform: rotateY(0deg);
}
.content-inner .content-front,
.content-inner .content-back {
	position: relative;
	transform-style: preserve-3d;
	perspective: 1000px;
	backface-visibility: hidden;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
.content-front {
	position:relative;
	cursor:pointer;
}
.content-front  .cf-inner:before {
	top:20px;
	left:20px;
	border-left:1px dotted;
	border-top:1px dotted;
}
.content-front .cf-inner:after {
	bottom:20px;
	right:20px;
	border-right:1px  dotted;
	border-bottom:1px dotted;
}
.bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-size: cover;
    background-attachment: scroll;
    background-position: center;
    background-repeat: repeat;
    background-origin: content-box;
}
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.4;
    z-index: 3;
}
.content-front  .cf-inner:before , .content-front  .cf-inner:after {
	content:'';
	position:absolute;
	width:50px;
	height:50px;
	border-color:rgba(255,255,255, 0.4);
	z-index:10;
}
.content-inner .content-back {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 0;
	transform: rotateY(180deg);
	background: #33bacc;
}
.content-inner .cf-inner {
	transform-style: preserve-3d;
	backface-visibility: hidden;
	width:100%;
	padding:150px 30px;
}
.content-inner .cf-inner .inner {
	align-items: stretch;
	transform-style: preserve-3d;
	perspective: 1000px;
	backface-visibility: hidden;
	transform: translateZ(95px) scale(0.81);
	text-align: center;
	position:relative;
	z-index:2;
} 
.content-back i {
	font-size:54px;
	position:relative;
}
.dec-icon {
	position:relative;
	display:inline-table;
	width:80px;
	height:80px;
}
.dec-icon:before , .dec-icon:after {
	content:'';
	position:absolute;
	width:60px;
	height:10px;
	border-radius:10px;
	background:rgba(255,255,255,0.1);
}
.dec-icon:before {
	left:-15px;
	top:12px;
}
.dec-icon:after  {
	right:-15px;
	bottom:12px;
}
.dec-icon i {
	z-index:3;
}
.content-back .dec-icon {
	top:-20px;
}

.content-inner .cf-inner .inner p {
	color:#fff;
}
.content-back:after  {
	position:absolute;
    content: '\25CF\25CF\25CF';
	width:50px;
	left:50%;
	bottom:30px;
	margin-left:-25px;
    font-size: 9px;
    letter-spacing: 4px;
}
.serv-num {
