	z-index:3;
	font-size:20px;
}
.content-inner .cf-inner .inner h2  {
	position:relative;
	font-size:22px;
	font-weight:800;
	padding-bottom:20px;
}
.content-inner .cf-inner .inner h2:after {
	content:'';
	position:absolute;
	top:-30px;
	margin-left:-25px;
	left:50%;
	width:50px;
	height:2px;
}
.content-inner .content-front h4{
	display:inline-block;
	color:#fff;
	text-transform:uppercase;
