/*footer*/

.footer{
    background-image: url(https://halongcomplex.sunworld.vn/wp-content/themes/halongcomplex/dist/images/bg-footer.png);
    min-height: 40vh;
    background-size: cover;
}
.inner-footer
{
    padding-top: 7%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.inner-footer li
{
    list-style: none;
    padding-right: 2%;
}
.inner-footer li a
{
    text-decoration: none;
    color: white;
    font-weight: bold;
}
.footer-three-icon
{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.menu-footer h1
{
    padding: 2% 0;
    font-weight: bold;
    letter-spacing: 2px;
    color: white;
}
.home-three-icon:nth-child(2)
{
   padding: 0 2%;
}
.home-three-icon a
{
    display: inline-block;
    justify-content: center;
    width: 3em;
    height: 3em;
    line-height: 3em;
    background-color: white;
    border-radius: 50%;
    text-align: center;
    color: #0fa3bb;
    transition: 0.4s;
}
.home-three-icon:hover a
{
    background-color: #ffba00;
    color: white;
}
.menu-footer h4
{
    margin-top: 6%;
    font-size: 15px;
}

