/* begin-foodZone */
*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* begin-menu */
.food-zone
{
    background-image: url(http://restabook.kwst.net/dark/images/bg/2.jpg);
    min-height: 90vh;
    background-size: cover;
    position: relative;
    background-position: center;
    background-attachment: fixed;
}
.container.food-container
{
    position:  absolute;
    z-index: 2;
    top: 0;
    left: 50%;
    transform: translate(-50%,0);
}
.food-zone::before
{
    content:'';
    width: 100%;
    height: 100%;
    background-image: -webkit-linear-gradient(left,#0000002b 0%,#12121359 50%,#0000004a 100%);
    position : absolute;
    top: 0;
    left: 0;
    z-index: 1;
}
.food-zone::after
{
    content:'';
    width: 100%;
    height : 6vh;
    position : absolute;
    bottom: -1%;
    left: 0;
    z-index: 2;
    background-image: url("http://restabook.kwst.net/dark/images/bg/brush-dec.png");
}
.navbar.navbar-expand-lg.navbar-light.bg-light.background-black
{
    background-color:#29292914 !important;
}
.nav-link
{
    display: inline-block;
    padding: .5rem 1.3rem !important;
}
.navbar-brand
{
    font-size:25px;
}
.navbar-nav.menu-right
{
    margin-left: 15%;
}
.navbar-nav.menu-right .layer1
{
    position: relative;
    padding: 8px 8px 8px 8px;
    transition: 0.4s;
}
.food-zone .navbar-nav.menu-right a.nav-link,.food-zone .navbar-brand
{
    color: #ffffff !important;
    font-weight: bold;
}
.food-zone .menu-product .layer2
{
    
    z-index: 4;
    visibility: hidden;
    opacity : 0;
    position: absolute;
    top : 200%;
    left: 51%;
    padding: 2% 3%;
    display: inline-block;
    background-color: #212121;
    transition: 0.4s;
    border-radius: 2%;
}
.navbar-nav.menu-right .menu-product:hover .layer2 
{
    top : 90%;
    visibility: visible ;
    opacity: 1 ;
}
.navbar-nav.menu-right .menu-product .layer2  ul  li
{
    list-style: none;
    padding: 2% 0;
    transition : 0.4s;
    width: 260px;
}
.navbar-nav.menu-right .menu-product .layer2  ul  li a
{
    color: #ffffffd1;
}
.navbar-nav.menu-right .menu-product .layer2  ul 
{
    visibility: hidden
}
.navbar-nav.menu-right .menu-product:hover .layer2  ul 
{
    visibility: visible
}
.navbar-nav.menu-right .menu-product .layer2 > ul > li:hover
{
    margin-left: 4%;
    
}
.food-zone .navbar-nav.menu-right .menu-product:hover .layer2  ul li a
{
    font-weight: bold;
    text-decoration: none;
    font-size: 15px;
    color : #ffffffd1;
}
.nav-item.buy-ticket a.nav-link
{
    background: linear-gradient(#fd0001, #d50e0e);
    color: #ffe93b !important;
    border-radius: 8%;
    padding: .5rem .5rem !important;
    display: inline-block;
}
.nav-item.buy-ticket a.nav-link::before
{
    content:url(data:image/png;base64,);
    margin-right: 5px;
    display: inline-block;
    vertical-align: text-top;
}
.header-banner {
    margin-top:20%;
    color: white;
}
.header-banner h2
{
    padding : 2% 0;
    position: relative;
}
.header-banner h2::before,.header-banner.food-detail h2::before
{
    content:"\25CF\25CF\25CF\25CF\25CF";
    width: 50px;
    height: 1px;
    font-size: 10px;
    letter-spacing: 4px;
    position: absolute;
    top: -20%;
    left : 1%;
}
.header-banner.food-detail h2::before
{
    left : 50%;
    transform: translate(-50%,0);
}
.header-banner h1
{
    font-size: 50px;
    font-weight: bold;
    padding-bottom: 4%;
}
a.check-out-menu 
{
    text-decoration: none;
    font-weight: bold;
    padding: 2% 2%;
    border: 1px solid #C19D60;
    color : white;
    background-color: #ffffff29;
    transition: 0.4s;
}
a.check-out-menu:hover
{
    background-color: #C19D60;
    color : white;
}
/* end-menu */
.main-top-food-banner {
    background-color: #212121;
    padding : 5% 0;
    color : white;
}
.main-top-food-banner .content-blog-main-top h3
{
    color : #C19D60;
}
.main-top-food-banner .content-blog-main-top h2
{
    position: relative;
    padding : 4% 0;
}

.main-top-food-banner .content-blog-main-top h2::after
{
    content: '\25CF\25CF\25CF\25CF\25CF\25CF';
    color: gray;
    font-size: 7px;
    letter-spacing: 4px;
    position: absolute;
    bottom: -30%;
    left: 0;
}

.main-top-food-banner .content-blog-main-top p 
{
    color: #ffffff9e;
}

.content-img-main-top .main-img-content
{
    position: relative;
    z-index: 10;
}
.content-img-main-top .main-img-collapse
{
    position: absolute;
    z-index: 2;
    width: 65%;
    top: -15%;
    left: 68%;
}
.main-mid-food-banner
{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    min-height: 60vh;
}
.mid-banner-food-left
{
    width : 50%;
    background-image: url(http://restabook.kwst.net/dark/images/bg/12.jpg);
    background-size: cover;
    position: relative;
}
.mid-banner-food-left::before
{
    content:'';
    width: 100%;
    height: 100%;
    background-image: -webkit-linear-gradient(left,#0000008a 0%,#12121359 50%,#00000061 100%);
    position : absolute;
    top: 0;
    left: 0;
    z-index: 1;

}
.quote-box
{
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    width: 90%;
    transform: translate(-50%,-50%);
    color:white;
}
.quote-box i
{
    font-size: 30px;
}
.mid-banner-food-left .signature
{
     display : flex;
     width: 100%;
     justify-content: flex-end;
}
.signature h4
{
    font-size: 17px;
    margin-right: 5%;
    display: flex;
    align-items: center;
    font-weight: 700;
}
.signature img
{
    vertical-align: middle;
}
.mid-banner-food-right
{
    width : 50%;
    background: #292929;
    background-size: cover;
    position: relative;
}
.mid-banner-food-right::before
{
    content: '';
    position: absolute;
    top: 50px;
    left: 50px;
    right: 50px;
    bottom: 50px;
    border: 1px dashed rgba(255,255,255,0.2);
    z-index: 2;
    box-shadow: 0px 0px 0px 50px rgb(255 255 255 / 3%);
}
.column-text
{
    position: relative;
    z-index: 3;
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 8%;
    color: white;
}
.column-text h2
{
    padding : 8% 0;
}
.column-text h2::after, .menu-food-banner-title h2::after
{
    content: '\25CF\25CF\25CF\25CF\25CF\25CF';
    color: #C19D60;
    font-size: 7px;
    letter-spacing: 4px;
}
.work-time
{
    width: 100%;
    z-index: 4;
    position: relative;
    margin-bottom: 30px;
    color:white;
    text-align: center;
}
.work-time::before
{
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -5px;
    width: 10px;
    bottom: 20px;
    background: url(http://restabook.kwst.net/dark/images/ver-separator.png) repeat-y;
}
.row::after
{
    clear: both;
}
.work-time h3
{
    font-size: 12px;
    color: #C19D60;
}
.work-time .hour
{
    font-size: 35px;
    font-weight: bold;
}
.mid-banner-food-right::after
{
    content:"";
    background-image: url(http://restabook.kwst.net/dark/images/bg/dec/7.png);
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
    background-size: cover;
    opacity: 0.2;
}
.bold-separator
{
    max-width: 400px;
    margin: 0 auto 20px;
    position: relative;
    text-align: center;
}
.bold-separator::before
{
    content: '';
    position: absolute;
    width: 30%;
    height: 1px;
    top: 50%;
    left :0;
    opacity: 0.3;
    background: #C19D60;
}
.bold-separator::after
{
    content: '';
    position: absolute;
    width: 30%;
    height: 1px;
    top: 50%;
    right :0;
    opacity: 0.3;
    background: #C19D60;
}
.bold-separator span
{
    width: 6px;
    height: 6px;
    display: inline-table;
    background: #fff;
    margin: 0 auto;
    border-radius: 100%;
}
.big-number-phone
{
    padding : 2% 3%;
    border : 1px solid #C19D60;
    width: 30%;
    margin : 0 auto;
    position: relative;
    color : white;
    font-weight: bold;
    letter-spacing:4px;
    cursor: pointer;
    transition: 0.4s;
    z-index: 3;
}
.big-number-phone:hover
{
    color: #C19D60;
}
.main-mid-choose-us
{
    background: #212121;
    position: relative;
    color: #C19D60;
    padding: 5% 0;
}
.main-mid-choose-us::before
{
    content:"";
    background-image: url("http://restabook.kwst.net/dark/images/bg/dec/section-bg.png");
    width: 100%;
    height: 100%;
    position:absolute;
    top: 0%;
    left: 0;
    background-size: cover;
    opacity: 0.2;
    z-index: 1;
}
.main-mid-choose-us h2
{
    color: white;
    padding-top: 1%;
    font-weight: bold;
    padding-bottom: 1%;
}
.main-mid-choose-us h2::after
{
    content: '\25CF\25CF\25CF\25CF\25CF\25CF';
    font-size: 7px;
    letter-spacing: 4px;
}
.content-inner {
	width: 100%;
	color: #fff;
	position: relative;
    z-index: 4;
	transform-style: preserve-3d;
	perspective: 1000px;
	backface-visibility: hidden;
	transition: .9s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.content-inner > * {
	backface-visibility: hidden;
	transition: .9s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.content-inner:hover .content-front {
	transform: rotateY(-180deg);
}
.content-inner:hover .content-food-back {
	transform: rotateY(0deg);
}
.content-inner .content-front,
.content-inner .content-food-back {
	position: relative;
	transform-style: preserve-3d;
	perspective: 1000px;
	backface-visibility: hidden;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
.content-front {
	position:relative;
	cursor:pointer;
}
.content-front  .cf-inner:before {
	top:20px;
	left:20px;
	border-left:1px dotted;
	border-top:1px dotted;
}
.content-front .cf-inner:after {
	bottom:20px;
	right:20px;
	border-right:1px  dotted;
	border-bottom:1px dotted;
}
.bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-size: cover;
    background-attachment: scroll;
    background-position: center;
    background-repeat: repeat;
    background-origin: content-box;
}
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.4;
    z-index: 3;
}
.content-front  .cf-inner:before , .content-front  .cf-inner:after {
	content:'';
	position:absolute;
	width:50px;
	height:50px;
	border-color:rgba(255,255,255, 0.4);
	z-index:10;
}
.content-inner .content-food-back {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 0;
	transform: rotateY(180deg);
	background: #292929;
}
.content-inner .cf-inner {
	transform-style: preserve-3d;
	backface-visibility: hidden;
	width:100%;
	padding:150px 30px;
}
.content-inner .cf-inner .inner {
	align-items: stretch;
	transform-style: preserve-3d;
	perspective: 1000px;
	backface-visibility: hidden;
	transform: translateZ(95px) scale(0.81);
	text-align: center;
	position:relative;
	z-index:2;
} 
.content-food-back i {
	font-size:54px;
	position:relative;
}
.dec-icon {
	position:relative;
	display:inline-table;
	width:80px;
	height:80px;
}
.dec-icon:before , .dec-icon:after {
	content:'';
	position:absolute;
	width:60px;
	height:10px;
	border-radius:10px;
	background:rgba(255,255,255,0.1);
}
.dec-icon:before {
	left:-15px;
	top:12px;
}
.dec-icon:after  {
	right:-15px;
	bottom:12px;
}
.dec-icon i {
	z-index:3;
}
.content-food-back .dec-icon {
	top:-20px;
}

.content-inner .cf-inner .inner p {
	color:#fff;
}
.content-food-back:after  {
	position:absolute;
    content: '\25CF\25CF\25CF';
	width:50px;
	left:50%;
	bottom:30px;
	margin-left:-25px;
    font-size: 9px;
    letter-spacing: 4px;
}
.serv-num {
	position:absolute;
	bottom:20px;
	left:30px;
	color:#fff;
	z-index:3;
	font-size:20px;
}
.content-inner .cf-inner .inner h2  {
	position:relative;
	font-size:22px;
	font-weight:800;
	padding-bottom:20px;
}
.content-inner .cf-inner .inner h2:after {
	content:'';
	position:absolute;
	top:-30px;
	margin-left:-25px;
	left:50%;
	width:50px;
	height:2px;
}
.content-inner .content-front h4{
	display:inline-block;
	color:#fff;
	text-transform:uppercase;
	font-size:12px;
	letter-spacing:2px;
	margin:0 4px;
}
.content-inner .cf-inner .inner h2:before {
	content:'';
	position:absolute;
	left:50%;
	bottom:10px;
	width:20px;
	height:2px;
	margin-left:-10px;
}
.menu-food-banner
{
    background-image: url(http://restabook.kwst.net/dark/images/bg/4.jpg);
    min-height: 100vh;
    background-size: cover;
    position: relative;
}
.menu-food-banner::before
{
    content:"";
    min-height:10vh;
    position:absolute;
    top:-1%;
    background-image: url(http://restabook.kwst.net/dark/images/bg/brush-dec_2.png);
    width: 100%;
    z-index: 2;
    background-size: cover;
}
.menu-food-banner::after
{
    content:"";
    position : absolute;
    z-index: 1;
    background-image: -webkit-linear-gradient(left,#0000008a 0%,#1212138a 50%,#0000007d 100%);
    width: 100%;
    height : 100%;
}
.menu-food
{
    position: relative;
    z-index: 4;
    max-width: 500px;
    left: 50%;
    transform: translate(-50%,0);
}
.menu-food-banner-title 
{
    position:absolute;
    z-index: 5;
    margin-top:30%;
    color: white;
}
.menu-food-banner-title h5
{
    color: #C19D60;
}
.menu-special
{
    position: relative;
    z-index: 4;
    max-width: 900px;
    left: 50%;
    transform: translate(-50%,0);
}
.food-menu-container
{
    position:absolute;
    z-index: 5;
    margin-top: 30%;
    color: white;
}
.menu-special-left ul li
{
    line-height: 50px;
    list-style: none;
    cursor: pointer;
}
.menu-special-right ul li
{
    list-style: none;
}
.menu-special-left ul li span
{
    font-size: 10px;
    margin-right: 1px;
}
.menu-special-right ul li span
{
    display: flex;
    color: #C19D60;
}
.menu-special-right ul li p
{
    font-size: 14px;
    color: #d2cbcb;
}
.menu-special-right ul li span h5
{
    margin-left: 1%;
    font-size: 18px;
    margin-right: 5%;
    color:white;
}
.menu-special-right ul li span p{
    opacity: 0.4;
}
.menu-special-right ul li span h6
{
   margin-left: 2%;
   color: #C19D60;
}
/* begin-foodZoneDetail */
.FoodZoneDetail-Main
{
    background: #212121;
    position: relative;
    color:white;
    padding: 6% 0;
}
.Menu-FoodZoneDetail-Main
{
    width: 70%;
    margin: 0 auto;
    position: relative;
}
.Menu-FoodZoneDetail-Main::after,.Menu-FoodZoneDetail-Main::before
{
    content: '';
    position: absolute;
    top: 50%;
    width: 120px;
    height: 1px;
    border-top: 1px dotted #C19D60;
}
.Menu-FoodZoneDetail-Main::after
{
    left: 15%;
}
.Menu-FoodZoneDetail-Main::before
{
    right: 15%;
}
.Menu-FoodZoneDetail-Main ul
{
    width: 45%;
    padding : 2% 2%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #303030;
}
.Menu-FoodZoneDetail-Main ul li
{
    cursor: pointer;
    list-style: none;
    font-weight: 500;
}
.menu-order
{
    background-color:#303030;
    padding: 1% 3%;
}
.menu-order-food-img img
{
    border-radius: 50%;
}
.menu-order-food-content h3
{
    padding-left: 4%;
    font-size: 16px;
    font-weight:bold;
    color:white;
}
.menu-order-food-content p
{
    padding-left: 4%;
    font-size: 12px;
    font-weight: bold;
    color: gray;
}
.menu-order-food-price
{
    color: #C19D60;
}
/* end-foodZoneDetail */

/*footer*/

.food-footer{
    background: #292929;
    min-height: 40vh;
    background-size: cover;
}
.inner-footer
{
    padding-top: 7%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.inner-footer li
{
    list-style: none;
    padding-right: 2%;
}
.food-footer .inner-footer li a
{
    text-decoration: none;
    color: #C19D60;
    font-weight: bold;
}
.footer-three-icon
{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.menu-food-footer h1
{
    padding: 2% 0;
    font-weight: bold;
    letter-spacing: 2px;
    color: #ffffffb8;
}
.home-three-icon:nth-child(2)
{
   padding: 0 2%;
}
.food-footer .home-three-icon a
{
    display: inline-block;
    justify-content: center;
    width: 3em;
    height: 3em;
    line-height: 3em;
    background-color: white;
    border-radius: 50%;
    text-align: center;
    color: #0fa3bb;
    transition: 0.4s;
}
.food-footer .home-three-icon:hover a
{
    background-color: #ffba00;
    color: white;
}
.menu-food-footer h4
{
    margin-top: 6%;
    font-size: 15px;
    color: #C19D60;
}
/* end-foodZone */
